@import '../../../styles/variables.scss';

.skip-links {
  position: fixed;
  top: 0;
  left: 0;
  padding: 0.5rem;
  z-index: 3;
  opacity: 0;
  pointer-events: none;
  &:focus-within {
    opacity: 1;
    pointer-events: all;
  }
}

.skip-link {
  display: inline-block;
  padding: 0.5rem;
  color: $primary;
  background-color: $black;
  &:hover,
  &:focus {
    outline: none;
    box-shadow: 0 0 1rem $primary;
  }
  & + & {
    margin-left: 0.5rem;
  }
}
